.content-wrapper.kanban {
	height: 1px;

	.content {
		height: 100%;
		overflow-x: auto;
		overflow-y: hidden;

		.container,
		.container-fluid {
			width: max-content;
			display: flex;
			align-items: stretch;
		}
	}
	.content-header + .content {
		height: calc(100% - ((2 * 15px) + (1.8rem * #{$headings-line-height})));
	}

	.card {
		.card-body {
			padding: 0.5rem;
		}

		&.card-row {
			width: 340px;
			display: inline-block;
			margin: 0 0.5rem;

			&:first-child {
				margin-left: 0;
			}

			.card-body {
				height: 100%;
				overflow-y: auto;
			}

			.card {
				.card-header {
					padding: 0.5rem 0.75rem;
				}
				.card-body {
					padding: 0.75rem;
				}
			}
		}
	}
	.btn-tool {
		&.btn-link {
			text-decoration: underline;
			padding-left: 0;
			padding-right: 0;
		}
	}
}
