//
// Component: Modals
//

// Overlay
.modal-dialog {
	.overlay {
		background-color: $black;
		display: block;
		height: 100%;
		left: 0;
		opacity: 0.7;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: ($zindex-modal + 2);
	}
}

// BG Color Variations Fixes
.modal-content {
	&.bg-warning {
		.modal-header,
		.modal-footer {
			border-color: $gray-800;
		}
	}

	&.bg-primary,
	&.bg-secondary,
	&.bg-info,
	&.bg-danger,
	&.bg-success {
		.close {
			color: $white;
			text-shadow: 0 1px 0 $black;
		}
	}
}

.dark-mode {
	.modal-header,
	.modal-footer {
		border-color: $gray-600;
	}
	.modal-content {
		background-color: $dark;

		&.bg-warning {
			.modal-header,
			.modal-footer {
				border-color: $gray-600;
			}
			.close {
				color: $dark !important;
				text-shadow: 0 1px 0 $gray-700 !important;
			}
		}

		&.bg-primary,
		&.bg-secondary,
		&.bg-info,
		&.bg-danger,
		&.bg-success {
			.modal-header,
			.modal-footer {
				border-color: $white;
			}
		}
	}
}
