#toast-container {
	// Background color
	.toast {
		background-color: $primary;
	}

	.toast-success {
		background-color: $success;
	}

	.toast-error {
		background-color: $danger;
	}

	.toast-info {
		background-color: $info;
	}

	.toast-warning {
		background-color: $warning;
	}
}

// full width fix
.toast-bottom-full-width .toast,
.toast-top-full-width .toast {
	max-width: inherit;
}
